import gsap from 'gsap';
import ScrollTrigger from 'gsap/ScrollTrigger';
import { getWindowSize } from '../../utils';
import { GLOBAL_VARS } from '../../utils/constants';
import SplitText from '../../vendors/SplitText';

class HomepageAnim {
	heroAnim() {
		const $hero = document.querySelector('.hero');
		if (!$hero) return;

		const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
		console.log(`safari: ${isSafari}`);

		const $heroBg = $hero.querySelector('.heroBgDesktop');
		const $heroBgSvgBlur = $heroBg.querySelector('.heroBg1 feGaussianBlur');
		const $heroBgSvgLetter = $heroBg.querySelector('.hero__bg_svg_letter');
		const $heroBgCover = $heroBg.querySelector('.heroBg1');
		const $heroBgCover2 = $heroBg.querySelector('.heroBg2');
		const $heroIcons = $hero.querySelectorAll('.heroDecor');
		const $heroTitle = $hero.querySelector('.heroTitle');
		const $heroSubtitle = $hero.querySelector('.heroSubtitle');

		const heroTl = gsap.timeline({
			delay: 0.2,
		});

		gsap.set([$heroBgCover, $heroBgCover2], {
			// x: '52.8vh',
			x: gsap.utils.wrap(['40vw', 0]),
			opacity: gsap.utils.wrap([1, 0]),
		});
		gsap.set($heroBgSvgBlur, {
			attr: { stdDeviation: '0 0' },
		});
		gsap.set([$heroTitle], {
			opacity: 0,
			filter: 'blur(2px)',
		});

		if ($heroIcons.length) {
			gsap.set($heroIcons, {
				opacity: 0,
				filter: 'blur(2px)',
			});
		}

		gsap.set($heroSubtitle, {
			opacity: 0,
		});
		gsap.set($heroIcons, {
			opacity: 0,
		});

		const subtitleLines = new SplitText($heroSubtitle, { type: 'lines' });

		let splitData;
		if (subtitleLines && subtitleLines.lines && subtitleLines.lines.length) {
			splitData = subtitleLines.lines.map((line) => {
				const { chars } = new SplitText(line, { type: 'chars' });

				chars.forEach((char, index) => {
					if (index !== 0) {
						if (char) {
							gsap.set(char, {
								opacity: 0,
							});
						}
					}
				});

				chars.splice(0, 1);

				return chars;
			});
		}

		heroTl
			.addLabel('start')
			.to($heroBg, {
				x: 0,
				duration: 1,
				ease: 'power2.out',
			}, 'start')
			.to($heroBgCover, {
				x: 0,
				duration: 1.2,
				ease: 'power2.out',
			}, 'start')
			.addLabel('blurs');
		if (isSafari) {
			heroTl.to([$heroBgCover, $heroBgCover2], {
				opacity: gsap.utils.wrap([0, 1]),
				delay: gsap.utils.wrap([0.5, 0]),
				duration: 0.6,
				ease: 'none',
			}, 'blurs');
		} else {
			heroTl.to($heroBgSvgBlur, {
				attr: { stdDeviation: '12 0' },
				duration: 0.6,
			}, 'blurs');
		}
		heroTl
			.to([$heroTitle], {
				opacity: 1,
				duration: 1.3,
				ease: 'power1.out',
			}, 'blurs')
			.to([$heroTitle], {
				filter: 'blur(0px)',
				duration: 1.7,
				ease: 'power1.inOut',
			}, 'blurs')
			.addLabel('split')
			.to($heroSubtitle, {
				opacity: 1,
				duration: 0.6,
				ease: 'power2.inOut',
			}, 'blurs+=1.2');

		if ($heroIcons.length) {
			heroTl
				.to($heroIcons, {
					opacity: 1,
					duration: 1,
					ease: 'power1.out',
				}, 'blurs')
				.to($heroIcons, {
					filter: 'blur(0px)',
					duration: 1.4,
					ease: 'power1.inOut',
				}, 'blurs');
		}

		if (!splitData.length) return;

		splitData.forEach((charsArr, index) => {
			if (!charsArr.length || !charsArr[0]) return null;

			heroTl
				.to(charsArr, {
					opacity: 1,
					stagger: 0.04,
				}, index === 0 ? 'split+=0.3' : '-=.3');

			return null;
		});
	}

	quoteAnim() {
		const $quote = document.querySelector('.quote');
		if (!$quote) return;

		const $quoteHead = $quote.querySelector('.quoteHead');
		const $quoteContent = $quote.querySelector('.quoteContent');
		const $quoteIcon = $quote.querySelector('.quoteIcon');
		const $quotePicture = $quote.querySelector('.quotePicture');
		const $quoteSign = $quote.querySelector('.quoteSign');
		const quoteTl = gsap.timeline({
			paused: true,
		});

		gsap.set($quotePicture, {
			opacity: 0,
			y: 150,
		});
		gsap.set($quoteContent, {
			opacity: 0,
			y: 70,
		});
		gsap.set($quoteHead, {
			opacity: 0,
		});
		gsap.set($quoteSign, {
			opacity: 0,
		});
		gsap.set($quoteIcon, {
			scaleY: 0,
			transformOrigin: 'top',
		});

		quoteTl
			.addLabel('start')
			.to($quotePicture, {
				y: 0,
				ease: 'Power1.in',
				duration: 0.8,
			}, 'start')
			.to($quotePicture, {
				opacity: 1,
				ease: 'Power1.inOut',
				duration: 1.4,
			}, 'start')
			.to($quoteContent, {
				y: 0,
				ease: 'Power1.in',
				duration: 0.8,
			}, 'start')
			.to($quoteIcon, {
				scaleY: 1,
				ease: 'Power1.inOut',
				duration: 0.4,
			}, '-=.6')
			.to($quoteSign, {
				opacity: 1,
				duration: 1.6,
			})
			.addLabel('end');

		if ($quoteContent) {
			quoteTl
				.to($quoteContent, {
					opacity: 1,
					ease: 'Power1.inOut',
					duration: 1,
				}, 'start');
		}
		if ($quoteHead) {
			quoteTl
				.to($quoteHead, {
					opacity: 1,
					ease: 'Power1.inOut',
					duration: 1,
				}, 'start');
		}

		const leaveTl = gsap.timeline();

		const handleEnter = () => {
			quoteTl.play();
		};

		const handleLeave = () => {
			leaveTl
				.addLabel('start')
				.to($quotePicture, {
					y: -150,
				}, 'start')
				.to($quoteContent, {
					y: -100,
				}, 'start')
				.to($quoteSign, {
					y: -100,
				}, 'start')
				.to($quoteHead, {
					y: -100,
				}, 'start');
		};
		const handleBack = () => {
			leaveTl
				.addLabel('start')
				.to($quotePicture, {
					y: 0,
				}, 'start')
				.to($quoteContent, {
					y: 0,
				}, 'start')
				.to($quoteSign, {
					y: 0,
				}, 'start')
				.to($quoteHead, {
					y: 0,
				}, 'start');
		};

		ScrollTrigger.create({
			trigger: $quote,
			start: 'center-=20% center',
			end: 'center top',
			onEnter: handleEnter,
			onLeave: handleLeave,
			onEnterBack: handleBack,
		});
	}

	innovationAnim() {
		const $innovation = document.querySelector('.innovation');
		if (!$innovation) return;

		const $innovationTitle = $innovation.querySelectorAll('.innovationTitle');
		const $innovationSubtitle = $innovation.querySelectorAll('.innovationSubtitle');
		const $innovationIcon = $innovation.querySelector('.innovationIcon');

		const innovationTl = gsap.timeline({
			paused: true,
		});

		gsap.set($innovationTitle, {
			opacity: 0,
			y: 100,
		});
		gsap.set($innovationSubtitle, {
			opacity: 0,
			y: 50,
		});
		gsap.set($innovationIcon, {
			scaleY: 0,
			transformOrigin: 'top',
		});

		innovationTl
			.addLabel('start')
			.to([$innovationTitle], {
				opacity: 1,
				ease: 'Power1.inOut',
				duration: 0.6,
			}, '+=0.1')
			.to([$innovationTitle], {
				y: 0,
				ease: 'Power1.inOut',
				duration: 0.72,
			}, '-=0.6')
			.to([$innovationSubtitle], {
				opacity: 1,
				ease: 'Power1.inOut',
				duration: 0.6,
			}, '+=0.1')
			.to([$innovationSubtitle], {
				y: 0,
				ease: 'Power1.inOut',
				duration: 0.72,
			}, '-=0.6')
			.to($innovationIcon, {
				scaleY: 1,
				ease: 'Power1.inOut',
				duration: 0.4,
			})
			.addLabel('end');

		const handleEnter = () => {
			innovationTl.play();
		};

		ScrollTrigger.create({
			trigger: $innovation,
			start: 'center+=30% bottom',
			end: 'botton top',
			onEnter: handleEnter,
		});
	}

	constructor() {
		this.init();
	}

	init() {
		if (getWindowSize().windowWidth < GLOBAL_VARS.mediaPoint1) return;
		gsap.registerPlugin(SplitText);
		gsap.registerPlugin(ScrollTrigger);

		this.heroAnim();
		this.quoteAnim();
		this.innovationAnim();
	}
}

export default HomepageAnim;
